@font-face {
  font-family: "Rubik Microbe";
  src: local("Rubik Microbe"),
    url("./fonts/RubikMicrobe-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Lavishly Yours";
  src: local("Lavishly Yours"),
    url("./fonts/LavishlyYours-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Quintessential";
  src: local("Quintessential"),
    url("./fonts/Quintessential-Regular.ttf") format("truetype");
}

.heading {
  font-family: "Rubik Microbe", sans-serif;
  font-size: 6.5rem;
  font-weight: 400;
  color: #7800af;
  /* animation: typing 3s steps(30) 1s 1 normal both; */
  overflow: hidden;
  margin: 0;
  z-index: 1;
}

.heading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.heading-container .sub-heading {
  font-family: "Lavishly Yours", sans-serif;
  font-size: 2.5rem;
  color: #fff;
  margin: 0;
}

.divider {
  width: 800px;
  height: 3px;
  background-color: #480069;
}

::-webkit-scrollbar-thumb {
  background: #480069;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background: #2c014e;
}

::-webkit-scrollbar {
  width: 5px;
}

.astro-button {
  display: flex;
  background-color: #480069;
  color: white;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s;
  justify-content: center;
  align-items: center;
  clip-path: polygon(10% 10%, 90% 5%, 85% 90%, 15% 85%);
  margin-right: 5px;
  font-family: "Quintessential", sans-serif;
  font-size: 1.3rem;
}

.astro-button:hover {
  background-color: #7800af;
  animation: jump 0.5s;
}

/* pop up animation continous*/
@keyframes pop-up {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

/* jump animation */
@keyframes jump {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

/* text type animation */
@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

/* need in bottom center */
.swipe-up {
  position: absolute;
  bottom: 50px;
  right: 50%;
  transform: translateX(50%);
  color: white;
  animation: jump 1s infinite;
  font-family: "Quintessential", sans-serif;
  font-size: 1.5rem;
}

.swipe-up .icon {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  background-color: #480069;
  color: white;
  border: none;
  border-radius: 50%;
  padding: 10px;
  cursor: pointer;
  font-size: 1.5rem;
  animation: pop-up 0.5s ease forwards;
  clip-path: polygon(10% 10%, 90% 5%, 85% 90%, 15% 85%);
}

.topbar-container {
  display: flex;
  gap: 20px;
  justify-content: flex-end;
  align-items: center;
  color: white;
  z-index: 1;
}

.topbar-container h1 {
  font-family: "Rubik Microbe", sans-serif;
  font-size: 2rem;
  margin: 0;
  color: white;
}

.section-2 {
  display: flex;
  min-height: 450px;
  /* border-top: 3px solid #480069;
  border-bottom: 3px solid #480069; */
  margin: 5px;
}

.section-2 .heading {
  font-size: 3.5rem;
  color: #fff;
  /* font-family: "Rubik Microbe", sans-serif; */
  font-weight: 400;
  margin: 0;
}

.section-2 .sub-heading {
  font-family: sans-serif;
  font-size: 1.5rem;
  color: #ffffff;
  margin: 0;
}

.section-2 .content {
  display: flex;
  width: 30%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 20px;
}

.section-2 .content-image {
  width: 70%;
  object-fit: cover;
  border-radius: 10px;
}

.footer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: black;
  font-family: "Quintessential", sans-serif;
}

.footer-container .footer-content .footer-quick-links {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 20px;
}

.footer-container .footer-content .footer-quick-links .quicklink {
  min-height: 350px;
  flex-grow: 1;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  /* text-align: center; */
}

.quicklink b {
  text-align: center;
}

.quicklink li {
  margin: 2px;
  margin-inline-start: 0;
}

.quicklink li svg {
  height: 15px;
  display: none;
}

.quicklink li:hover {
  text-decoration: underline;
  cursor: pointer;
  word-spacing: 5px;
}

.quicklink li:hover svg {
  display: inline-block;
}


.footer-container .footer-content {
  width: 90vw;
}

.quick-link-horoscopes {
  list-style-type: none;
  margin-inline-start: 0;
}

.footer-container .copyright {
  border-top: 1px solid white;
  font-size: 0.8rem;
  text-align: center;
  padding: 10px 0;
}


.footer-container .sub-heading {
  font-size: 1rem;
  margin: 0;
}

.footer-container .heading {
  color: #fff;
}

.astro-image {
  object-fit: fill;
  z-index: -1;
  animation: rotate 120s linear infinite;
  border-radius: 100%;
  position: relative;
  mix-blend-mode: hard-light;
  left: 40%;
  user-select: none;
  pointer-events: none;
}

.astro-image-container {
  position: absolute;
  width: -webkit-fill-available;
  overflow: hidden;
  /* Keeps the image behind the text */
}

/* rotate animation */
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 600px) {
  .heading {
    font-size: 3.5rem;
  }

  .divider {
    width: 100%;
  }

  .heading-container .sub-heading {
    font-size: 1.5rem;
  }

  .topbar-container {
    justify-content: space-between;
  }
}